
































import md5 from "@/common/SDK/md5.min.js";
import api from "../../utils/api.js";
import check from "../../utils/check.js";
var app = getApp();
export default {
	data() {
		return {
			phoneNumber: '',
			code: '',
			isShowOauth: false,
			showProvider: {
				weixin: true,
				qq: false,
				sinaweibo: false
			},
			getCodeText: '获取验证码',
			getCodeBtnColor: "#ffffff",
			getCodeisWaiting: false
		}
	},
	onShow() {
		// 页面显示
	},
	onLoad() {
		//APP显示第三方登录
		//


		this.getProvider();
	},
	methods: {
		oauthLogin(provider) {
			uni.showLoading();
			//第三方登录
			uni.login({
				provider: provider,
				success: (loginRes) => {
					console.log("success: " + JSON.stringify(loginRes));
					//案例直接获取用户信息，一般不是在APP端直接获取用户信息，比如微信，获取一个code，传递给后端，后端再去请求微信服务器获取用户信息
					uni.getUserInfo({
						provider: provider,
						success: (infoRes) => {
							console.log('用户信息：' + JSON.stringify(infoRes.userInfo));
							uni.setStorage({
								key: 'UserInfo',
								data: {
									username: infoRes.userInfo.nickName,
									face: infoRes.userInfo.avatarUrl,
									signature: '个性签名',
									integral: 0,
									balance: 0,
									envelope: 0
								},
								success: function() {
									uni.hideLoading()
									uni.showToast({
										title: '登录成功',
										icon: "success"
									});
									setTimeout(function() {
										uni.navigateBack();
									}, 300)
								}
							});
						}
					});
				},
				fail: (e) => {
					console.log("fail: " + JSON.stringify(e));
				}
			});
		},
		getCode() {
			uni.hideKeyboard()
			if (this.getCodeisWaiting) {
				return;
			}
			if (!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.phoneNumber))) {
				uni.showToast({
					title: '请填写正确手机号码',
					icon: "none"
				});
				return false;
			}
			this.getCodeText = "发送中..."
			this.getCodeisWaiting = true;
			this.getCodeBtnColor = "rgba(255,255,255,0.5)"
			//模拟请求效果
			setTimeout(() => {
				uni.showToast({
					title: '验证码已发送',
					icon: "none"
				});
				//this.code=1234;
				uni.request({
					url: api.AuthRegisterCaptcha, //登录接口
					method: 'POST',
					header: {
						'Content-Type': 'application/json',
						'X-Litemall-Token': uni.getStorageSync('token')
					},
					data: {
						mobile: this.phoneNumber
					},
					success: function(res) {
						let list = JSON.stringify(res.data);
						console.log("验证码结果返回：" + list);
						if (res.data.errno == 0) {

							uni.showToast({
								title: '发送成功',
								content: '验证码已发送',
								showCancel: false
							});

						} else {
							uni.showToast({
								title: res.data.errmsg,
								content: res.data.errmsg,
								showCancel: false
							});

						}
					},
					fail: function(e) {
						console.log("发送失败");
						uni.showToast({
							title: '后台服务无反应，请稍后再试或联系管理员'
						});

					}


				});
				this.setTimer();
			}, 1000)
		},
		setTimer() {
			let holdTime = 60;
			this.getCodeText = "重新获取(60)"
			this.Timer = setInterval(() => {

				if (holdTime <= 0) {
					this.getCodeisWaiting = false;
					this.getCodeBtnColor = "#ffffff";
					this.getCodeText = "获取验证码"
					clearInterval(this.Timer);
					return;
				}
				this.getCodeText = "重新获取(" + holdTime + ")"
				holdTime--;

			}, 1000)
		},
		getProvider() {
			//获取第三方登录服务
			uni.getProvider({
				service: 'oauth',
				success: (res) => {
					let len = res.provider.length;
					for (let i = 0; i < len; i++) {
						//有服务才显示按钮图标
						this.showProvider[res.provider[i]] = false;
					}
					if (res.provider.length == 0) {
						this.isShowOauth = false;
					}
				}
			});
		},
		toPage(page) {
			uni.hideKeyboard()
			uni.navigateTo({
				url: page
			});
		},
		doLogin() {
			uni.hideKeyboard();
			//验证手机号码
			if (!(/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.phoneNumber))) {
				uni.showToast({
					title: '请填写正确手机号码',
					icon: "none"
				});
				return false;
			}
			uni.showLoading({
				title: '提交中...',
			})

			setTimeout(() => {
				//let md5PW = md5(this.passwd)
				//plus.nativeUI.alert(md5PW);
				uni.request({
					// key: 'UserList',
					url: api.AuthLoginByPhone, //登录接口
					method: 'POST',
					header: {
						'Content-Type': 'application/json'
					},
					data: {
						mobile: this.phoneNumber,
						code: this.code
					},
					success: (res) => {
						let list = JSON.stringify(res.data);
						console.log("登录结果返回：" + list);
						if (res.data.errno == 0) {
							app.globalData.hasLogin = true;
							const data = res.data.data;
							const userInfo = data.userInfo;
							self.userInfo = userInfo;
							uni.setStorageSync('userInfo', userInfo);
							uni.setStorageSync('token', data.token);



							console.log("返回数据状态:" + userInfo);
							console.log("返回数据状态:" + userInfo.nickName);
							uni.setStorage({
								key: "userinfo",
								data: {
									userInfo,
									isfirst: false
								},
								success: function() {
									uni.switchTab({
										url: '../tabBar/user'
									});
								}
							});
						} else {
							uni.hideLoading()
							uni.showModal({
								title: '错误信息',
								content: res.data.errmsg,
								showCancel: false
							});
						}
					},
					fail: function(e) {
						uni.hideLoading()
						uni.showToast({
							title: '登录失败请稍后再试'
						});
					}
				});
			}, 1000)
		}
	}
}
